import React, { useState, useEffect } from "react";
import "./App.css";
import clickImage from "./assets/click-image.jpg";
import energyIcon from "./assets/energy-icon.png";

function App() {
  const [score, setScore] = useState(0);
  const [clickValue, setClickValue] = useState(1);
  const [upgradeCost, setUpgradeCost] = useState(10);
  const [level, setLevel] = useState(1);
  const [energy, setEnergy] = useState(100);
  const [maxEnergy, setMaxEnergy] = useState(100);
  const [energyRegen, setEnergyRegen] = useState(5);
  const [isClickBlocked, setIsClickBlocked] = useState(false);
  const [clickPosition, setClickPosition] = useState(null);
  const [isCoinPopupVisible, setIsCoinPopupVisible] = useState(false);
  const [activeTab, setActiveTab] = useState("click");

  const [energyUpgradeCost, setEnergyUpgradeCost] = useState(50);
  const [energyRegenUpgradeCost, setEnergyRegenUpgradeCost] = useState(100);
  const [energyUpgradeLevel, setEnergyUpgradeLevel] = useState(1);
  const [energyRegenUpgradeLevel, setEnergyRegenUpgradeLevel] = useState(1);

  // Инициализация Telegram WebApp
  useEffect(() => {
    if (window.Telegram?.WebApp) {
      window.Telegram.WebApp.expand();
      loadProgress(); // Загружаем прогресс при старте
    }
  }, []);

  // Плавное восстановление энергии
  useEffect(() => {
    const intervalTime = 1000 / energyRegen;
    const energyTimer = setInterval(() => {
      setEnergy((prevEnergy) => Math.min(prevEnergy + 1, maxEnergy));
    }, intervalTime);

    return () => clearInterval(energyTimer);
  }, [energyRegen, maxEnergy]);

  // Клик по изображению
  const handleImageClick = (e) => {
    if (energy > 0 && !isClickBlocked) {
      setScore(score + clickValue);
      setEnergy(energy - clickValue);

      const x = e.clientX;
      const y = e.clientY;
      setClickPosition({ x, y });

      setIsCoinPopupVisible(true);
      setTimeout(() => setIsCoinPopupVisible(false), 200);

      if (energy - clickValue <= 0) {
        setIsClickBlocked(true);
        setTimeout(() => setIsClickBlocked(false), 3000);
      }
    }
  };

  // Улучшение клика
  const handleUpgrade = () => {
    if (score >= upgradeCost) {
      setScore(score - upgradeCost);
      setClickValue(clickValue + 1);
      setLevel(level + 1);
      setUpgradeCost(Math.floor(upgradeCost * 3));
    } else {
      alert("Не хватает сигма-коинов для улучшения!");
    }
  };

  // Улучшение ёмкости энергии
  const handleEnergyUpgrade = () => {
    if (score >= energyUpgradeCost) {
      setScore(score - energyUpgradeCost);
      setMaxEnergy(maxEnergy + 50);
      setEnergyUpgradeLevel(energyUpgradeLevel + 1);
      setEnergyUpgradeCost(Math.floor(energyUpgradeCost * 3));
    } else {
      alert("Не хватает сигма-коинов для прокачки ёмкости энергии!");
    }
  };

  // Улучшение восстановления энергии
  const handleEnergyRegenUpgrade = () => {
    if (score >= energyRegenUpgradeCost) {
      setScore(score - energyRegenUpgradeCost);
      setEnergyRegen(energyRegen + 5);
      setEnergyRegenUpgradeLevel(energyRegenUpgradeLevel + 1);
      setEnergyRegenUpgradeCost(Math.floor(energyRegenUpgradeCost * 3));
    } else {
      alert("Не хватает сигма-коинов для прокачки восстановления энергии!");
    }
  };

  // ✅ Сохранение прогресса в боте
  const saveProgress = () => {
    const user = window.Telegram.WebApp.initDataUnsafe?.user;
    if (!user) {
      alert("Ошибка: не удалось получить user_id");
      return;
    }

    const botUsername = "@Ponzxc_bot"; // Укажи ник бота без "@"
    const saveUrl = `https://t.me/${botUsername}?start=save score=${score} click=${clickValue} maxEnergy=${maxEnergy} regen=${energyRegen}`;

    window.open(saveUrl, "_blank"); // Открывает Telegram с командой сохранения
  };

  // ✅ Загрузка прогресса из бота
  const loadProgress = () => {
    const user = window.Telegram.WebApp.initDataUnsafe?.user;
    if (!user) {
      alert("Ошибка: не удалось получить user_id");
      return;
    }

    const botUsername = "YOUR_BOT_USERNAME"; // Укажи ник бота без "@"
    const loadUrl = `https://t.me/${botUsername}?start=load`;

    window.open(loadUrl, "_blank"); // Открывает Telegram с командой загрузки
  };

  return (
    <div className="App">
      <h1>Кликай сигму - получай TON</h1>
      <div className="score-board">
        <p>Сигма-коинов: <span>{score}</span></p>
      </div>

      <div className="energy-board">
        <img src={energyIcon} alt="Energy" className="energy-icon" />
        <p>Энергия: <span>{energy}</span>/{maxEnergy}</p>
      </div>

      <div className="tabs">
        <button className="tab-button" onClick={() => setActiveTab("click")}>Кликать</button>
        <button className="tab-button" onClick={() => setActiveTab("upgrade")}>Прокачки</button>
      </div>

      {activeTab === "click" && (
        <div className="click-tab">
          <div className="image-container" onClick={handleImageClick}>
            <img src={clickImage} alt="Кликни меня!" className="clickable-image" />
            {isCoinPopupVisible && clickPosition && (
              <div className="coin-popup" style={{
                left: clickPosition.x - 30,
                top: clickPosition.y - 50,
                position: "absolute",
                color: "white",
                fontSize: "20px",
                fontWeight: "bold",
                pointerEvents: "none",
                opacity: isCoinPopupVisible ? 1 : 0,
                transition: "opacity 0.2s",
              }}>+{clickValue}</div>
            )}
          </div>
        </div>
      )}

      {activeTab === "upgrade" && (
        <div className="upgrade-tab">
          <div className="upgrade-container">
            <button onClick={handleUpgrade}>Прокачать (Стоимость: {upgradeCost} монет) - Сейчас: {clickValue} монет за клик</button>
            <p>Уровень прокачки: {level}</p>
          </div>

          <div className="energy-upgrades">
            <button onClick={handleEnergyUpgrade}>Прокачать ёмкость энергии (Стоимость: {energyUpgradeCost} монет)</button>
            <p>Уровень прокачки ёмкости энергии: {energyUpgradeLevel}</p>

            <button onClick={handleEnergyRegenUpgrade}>Прокачать восстановление энергии (Стоимость: {energyRegenUpgradeCost} монет)</button>
            <p>Уровень прокачки восстановления энергии: {energyRegenUpgradeLevel}</p>
          </div>
        </div>
      )}

      <div className="buttons">
        <button onClick={saveProgress}>💾 Сохранить</button>
        <button onClick={loadProgress}>🔄 Загрузить</button>
      </div>
    </div>
  );
}

export default App;
